
.header {
    background: $black;
    padding: 20px 0 22px 0;
    .wrapper {
        @include justify-content(space-between);
        @include align-items(center);
    }
    img {
        width: 100%;
    }
    .logo {
        max-width: 80px;
    }
}
.nav {
    &-a {
        text-decoration: none;
        color: $white;
        display: inline-block;
        font-family: $sa;
        font-size: 14px;
        transition: color $tr;
        font-weight: 500;
        & + & {
            margin-left: 40px;
        }
        &:hover {
            color: $orange;
        }
    }
}
.logo {
    text-decoration: none;
}
