.form {
    min-width: 360px;
    &-head {
        background: #f5f5f5;
        text-align: center;
        color: $dark;
        font-family: $sa;
        font-weight: bold;
        font-size: 20px;
        text-transform: uppercase;
        padding: 15px 0;
        
        border-radius: 5px 5px 0 0;
    }
    &-block {
        padding: 28px 38px;
        background: #fff;
    }
    &-bottom {
        &-btn {
            background: $orange;
            color: $white;
            border-radius: 0 0 5px 5px;
            width: 100%;
            font-size: 15px;
        }
    }
}
