.btn {
    display: inline-block;
    padding: 14px 30px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    border: none;
    
    cursor: pointer;
        &-white {
            border: 1px solid rgba(#f5f5f5,0.5);
            outline: 1px solid #f5f5f5;
            
            color: $white;
            transition: all $tr;
            &:hover {
                background: $white;
                color: $orange;
            }
        }
    &-orange {
        background: $orange;
        color: $white;
        border-radius: 4px;
        transition: all $tr;
        &:hover {
            background: $dark;
        }
    }
}