
.tab {
    @include align-items(flex-start);
    &s {
        @include flex-direction(column);
        min-width: 110px;
        text-align: center;
        border: 1px solid #c7c7c7;
        border-radius: 4px;
        outline: none;
        
        text-transform: uppercase;
        
        margin-right: 100px;
        &-a {
            padding: 50px 0;
            text-decoration: none;
            font-size: 15px;
            font-weight: bold;
            background: $dark;
            color: $white;
            & + & {
                border-top: 1px solid #c7c7c7;
            }
            &.active {
                background: $orange;
            }
        }
    }
}
