
.cloud {
    &-icon {
        min-width: 40px;
        min-height: 40px;
        width: 40px;
        height: 40px;
        
        margin-right: 30px;
        
        border-radius: 50%;
        border: 2px solid $orange;
        
        color: $orange;
    }
    &-head {
        font-weight: bold;
        color: #212121;
    }
}

.awesome {
    
    text-align: center;
    p {
        text-align: center;
    }
    &-icon {
        width: 66px;
        height: 66px;
        border: 2px solid $orange;
        border-radius: 50%;
        margin: 0 auto 30px auto;
    }
    &-head {
        font-size: 20px;
        font-weight: bold;
        color: $dark;
        padding-bottom: 30px;
    }
}

.price {
    &-block {
        @include justify-content(space-between);
        @include align-items(flex-end);
        text-align: center;
    }
    &-item {
        width: 30%;
    }
    &__top {
        background: $dark;
        &-head {
            padding: 20px 0;
            color: $white;
            font-weight: bold;
            font-family: $sa;
            border-bottom: 1px solid $white;
        }
        &-center {
            color: $white;
            font-size: 35px;
            padding-bottom: 16px;
            span {
                font-size: 94px;
            }
        }
        i {
            display: block;
            color: rgba($white,.5);
            font-family: $sa;
            padding-bottom: 21px;
        }
    }
    &-orange {
        background: $orange;
        padding: 2.5px 0;
        color: $white;
        font-weight: bold;
        font-family: $sa;
        span {
            display: block;
            padding: 12.5px 0;
        }
    }
    &__list {
        background: #fff;
        font-family: $sa;
        text-transform: uppercase;
        color: $textgrey;
        &-item {
            padding: 12px 0;
            border-top: $lgrey 1px solid;
            
        }
    }
}

.comment {
    &-top {
        background: $lgrey;
        padding: 23px 10px;
        border-radius: 4px;
        position: relative;
        margin-bottom: 30px;
        &:after {
            content: '';
            display: block;
            position: absolute;
            bottom: -23px;
            width: 0;
            height: 0;
            border: 9px solid rgba(0,0,0,0);
            border-top: 14px solid $lgrey;
            left: 19px;
        }
    }
}
