@import "variables";
@import "mixins";

html,
body {
    margin: 0;
    padding: 0;
    font-family: $re;
}
.flex {
    @include flexbox;
}
.wrapper {
    max-width: 1150px;
    padding: 0 10px;
    margin: 0 auto;
}
.section {
    &__first {
        background: url(../images/bg/fs.jpg) no-repeat top center;
        background-size: cover;
        padding: 120px 0;
        color: $white;
        p {
            margin-bottom: 40px;
            color: $white;
        }
    }
    &__media {
        padding: 21px 0 23px 0;
        box-shadow: 0 1px 3px rgba($black,.3);
        b {
            display: block;
            padding-bottom: 5px;
            color: #333333;
            font-size: 20px;
            font-weight: bold;
        }
        p {
            font-size: 14px;
        }
    }
    &__text {
        
        padding: 115px 0;
        .wrapper {
            @include justify-content(space-between);
            @include align-items(center);
        }
        &-block {
            max-width: 42%;
            &-long {
                max-width: 60%;
            }
        }
        &-img {
            img {
                display: block;    
            }
        }
        &-sh {
            box-shadow: 0 0 25px rgba($black,.2);
        }
    }
    &-bg {
        background: #f5f5f5;
    }
    &__awesome {
        padding: 100px 0;
        text-align: center;
    }
}
.h1 {
    font-family: $re;
    font-size: 36px;
    font-weight: 400;
    &-line {
        &:after {
            content: '';
            display: block;
            width: 64px;
            height: 2px;
            background: $white;
            margin: 30px 0 38px 0;
        }
    }
}
p {
    padding: 0;
    margin: 0;
    font-size: 16px;
    color: $textgrey;
    line-height: 23px;
    
    padding-bottom: 27px;
}
.h2 {
    margin: 0;
    padding: 0;
    color: #000;
    font-size: 20px;
    padding-bottom: 57px;
    &-orange {
        padding-bottom: 0;
        &:after {
            content: '';
            display: block;
            width: 64px;
            height: 2px;
            background: $orange;
            margin: 30px 0; 
        } 
    }
    &-center {
        font-size: 36px;
        font-display: $re;
        font-weight: 400;
        @include flex-direction(column);
        @include align-items(center);
        
        padding-bottom: 0;
        &:after {
            content: '';
            display: inline-block;
            width: 64px;
            margin: 30px;
            height: 2px; 
            background: $orange;
        }
    }
    &-p {
        padding-bottom: 53px
    }
}
.color {
    &-orange {
        color: $orange;
    }
}
.label {
    position: relative;
    display: block;
    width: 100%;
    & + & {
        margin-top: 38px;
    }
    &-span {
        position: absolute;
        color: $grey;
        top: 50%;
        left: 13px;
        transform: translate(0, -50%);
        font-size: 14px;
        transition: all $tr;
        
        cursor: text;
    }
    &-input {
        background: none;
        border: none;
        border-bottom: 1px solid #d6d6d6;
        display: block;
        width: 100%;
        font-size: 14px;
        padding: 10px 13px;
        
        outline: none;
        &:focus {
            & + .label-span {
                top: 0%;
                font-size: 10px;
            }
            
        }
    }
}
